<template>
  <div class="examples">
    <div class="example" v-for="(value, key) in examples" :class="{ 'vue': key === 'exampleVue' }">
      <component :is="key"></component>
    </div>
  </div>
</template>

<script>
  import examples from 'components/vue-codemirror/examples'
  export default {
    name: 'app',
    data() {
      return {
        examples
      }
    },
    components: examples
  }
</script>

<style lang="scss">
  .examples {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;

    > .example {
      width: 48%;
      margin-bottom: 2em;

      &.vue {
        width: 100%;
      }

      .md-card {
        box-shadow: none;

        .md-card-actions {
          background-color: #fafafa;
          justify-content: space-between;

          .md-subhead {
            padding: 8px;
            text-transform: capitalize;
          }
        }

        .md-card-media {

          .vue {
            height: 300px;
            display: flex;
            justify-content: space-between;

            .codemirror,
            .pre {
              height: 300px;
              width: 50%;
              overflow-y: auto;
              font-family: Menlo,Monaco,Consolas,Courier New,monospace;
            }

            .codemirror {
            }

            .pre {
              display: block;
              padding: 11px;
              font-size: 12px;
              line-height: 1.846;
              word-break: break-all;
              word-wrap: break-word;
              color: #212121;
              background-color: #eee;
            }
          }
        }
      }
    }
  }
</style>



