<template>
  <md-card>
    <md-card-actions v-md-ink-ripple>
      <div class="md-subhead">
        <span>mode: {{ editorOption.mode }}</span>
        <span>&nbsp;&nbsp;&nbsp;</span>
        <span>theme: {{ editorOption.theme }}</span>
      </div>
      <md-button class="md-icon-button"
                 target="_blank"
                 href="https://github.com/surmon-china/vue-codemirror/tree/master/examples/04-text-html.vue">
        <md-icon>code</md-icon>
      </md-button>
    </md-card-actions>
    <md-card-media>
      <div class="codemirror">
        <!-- codemirror -->
        <codemirror v-model="code" :options="editorOption"></codemirror>
      </div>
    </md-card-media>
  </md-card>
</template>

<script>

  // require active-line.js
  require('codemirror/addon/selection/active-line.js')

  // autoCloseTags
  require('codemirror/addon/edit/closetag.js')
  
  export default {
    data() {
const code =
`<html style="color: green">
  <!-- this is a comment -->
  <head>
    <title>HTML Example</title>
  </head>
  <body>
    <!-- <p> -->
    <!--  -->
    The indentation tries to be <em>somewhat &amp;quot;do what
    I mean&amp;quot;</em>... but might not match your style.
    <!-- Piece of the CodeMirror manual, 'translated' into Arabic by Google Translate -->
    <dl>
      <dt id=option_value><code>value (string or Doc)</code></dt>
      <dd>  .     .  .</dd>
      <dt id=option_mode><code>mode (string or object)</code></dt>
      <dd> .        
        .           
      MIME   .         
          <code>name</code>    
      (   <code>{name: "javascript", json: true}</code>).
                 
      .    CodeMirror      MIME
        <code>CodeMirror.modes</code>
       <code>CodeMirror.mimeModes</code> .   
            MIME 2 
      .</dd>
      <dt id=option_theme><code>theme (string)</code></dt>
      <dd>   .      CSS 
       <code>.cm-s-[name]</code>    (
      <a href="../theme/"><code>theme</code></a>   ).
        <code>"default"</code>     
       <code>codemirror.css</code>.     
              <code>"foo bar"</code>
          <code>cm-s-foo</code>  <code>cm-s-bar</code>
        .</dd>
    </dl>
  </body>
</html>
`
      return {
        code,
        editorOption: {
          tabSize: 4,
          styleActiveLine: true,
          lineNumbers: true,
          autoCloseTags: true,
          line: true,
          mode: 'text/html',
          theme: 'ambiance'
        }
      }
    }
  }
</script>




